.wrap{
    max-width: 792px;
    margin: 80px auto 136px;

    .main{
        display: flex;
        justify-content: space-between;
        padding: 40px 0 0;

        .left{
            width: 332px;

            .item{
                display: flex;
                align-items: center;
                height: 100px;
                background: #181818;
                border: 1px solid rgba(0, 0, 0, 0.08);
                box-shadow: 0px 8px 32px rgba(138, 11, 11, 0.05);
                border-radius: 16px;
                margin: 0 0 22px;
                box-sizing: border-box;
                padding: 26px 24px;

                .info{
                    margin: 0 0 0 16px;
                    .symbol{
                        font-family: 'Poppins';
                        font-style: normal;
                        font-weight: 400;
                        font-size: 16px;
                        line-height: 20px;
                        color: rgba(#fff, .6);
                    }
                    .num{
                        font-family: 'Kanit';
                        font-style: normal;
                        font-weight: 400;
                        font-size: 24px;
                        line-height: 30px;
                        color: #fff;
                        margin: 4px 0 0;
                    }
                }
            }
        }
        .right{
            .transferBox{
                position: relative;
                height: 4px;
                .transferBtn{
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    border: 1px solid #373737;
                    background: #181818;
                    height: 30px;
                    width: 30px;
                    padding: 0;
                    .transferIcon{
                        fill: rgba(#fff, .6);
                        transition: all 0.3s;
                        height: 30px;
                        width: 30px;
                        &:hover{
                            fill: rgba(255, 255, 255, 1);
                        }
                    }
                }
            }
        }
    }
}